* {
  margin: 0;
  padding: 0;
}

header {
  width: 1024px;
  margin: 0 auto;
  padding: 0 50px;
  box-shadow: 0 2px 4px #0000000d;
}

header .header__logo {
  height: 100%;
  display: block;
}

header .nav__list {
  list-style: none;
  display: flex;
}

header .nav__item {
  margin-right: 20px;
  position: relative;
}

header .nav__item:last-child {
  margin-right: 0;
}

header .nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

header .nav__link:hover, header .is-active {
  color: #00acdc;
}

header .is-active:before {
  content: "";
  background: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (width >= 1200px) {
  header {
    width: 1200px;
  }
}

.card__button, .card__name, .card, header .nav__link {
  transition: all .3s ease-in-out;
}

.card__price, .card__rating, header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
  line-height: 38px;
  text-decoration: none;
  display: block;
}

.card__rating .card__reviews, .card__code {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  margin-bottom: 46px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__rating {
  margin-bottom: 24px;
}

.card__rating .card__reviews {
  line-height: 16px;
}

.card__price {
  margin-bottom: 16px;
}

.card__note {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: Roboto, sans-serif;
}

.grid-container {
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(4, 248px);
  justify-content: center;
  justify-items: center;
  width: 100vw;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 1023px) {
  .grid-container {
    grid-template-columns: repeat(3, 248px);
  }
}

@media screen and (width <= 767px) {
  .grid-container {
    grid-template-columns: repeat(2, 248px);
  }
}

@media screen and (width <= 487px) {
  .grid-container {
    grid-template-columns: auto;
  }
}

